const mutations = {
    //初始化字典
    initLocalDict(state) {
        //state.dict[dictName] = [];没有初始值的话没有用，要用concat/push/splice等方法才能使state中的数据即时响应到页面
        if (sessionStorage.getItem('Polaris:newdict')) {
            const dp = JSON.parse(sessionStorage.getItem('Polaris:newdict'));
            for (let p in dp) {
                if (dp[p] && dp[p].length && state.dict[p] && !state.dict[p].length)
                    state.dict[p] = state.dict[p].concat(dp[p]);
            }
        }
    },
}
const actions = {
    queryDictData: (context, dictArr) => {
        return new Promise((resolve, reject) => {
            if (dictArr && Object.prototype.toString.call(dictArr) !== '[object Array]') {
                dictArr = [dictArr]
            }
            let arr = [];
            let oldLocalDict = JSON.parse(sessionStorage.getItem('Polaris:newdict') || "{}") || {};
            dictArr.forEach((dictName, i) => {
                if (!(context.state.dict[dictName] && context.state.dict[dictName].length)) {
                    if (context.state.dictconfig[dictName]) arr.push(context.state.dictconfig[dictName]);
                } else {
                    if (oldLocalDict[dictName] && oldLocalDict[dictName].length)
                        context.state.dict[dictName] = [].concat(oldLocalDict[dictName]);
                    else if (context.state.dict[dictName])
                        oldLocalDict[dictName] = context.state.dict[dictName];
                }
            })
            if (!arr.length) {
                sessionStorage.setItem('Polaris:newdict', JSON.stringify(context.state.dict))
                resolve(context.state.dict);
                return;
            }
            Vue.prototype.$http.post('/dictionary/queryList.', {
                keys: arr
            }).then((res) => {
                let result = res.data;
                if (!result.success) return $Message.error(result.errorMessage || '字典请求失败');
                let newDict = result.data;//对象
                let keyArr = Object.keys(newDict);
                keyArr.forEach((p, i) => {
                    let dname = context.state.invertDictKeys[p];
                    if (newDict[p] && newDict[p].length) {
                        context.state.dict[dname] = [].concat(newDict[p]);
                    }
                    //最后一个字典时保存到本地，并返回给页面。
                    if (i == keyArr.length - 1) {
                        sessionStorage.setItem('Polaris:newdict', JSON.stringify(context.state.dict))
                        resolve(context.state.dict);
                    }
                })
            }).catch((err) => {
                sessionStorage.setItem('Polaris:newdict', JSON.stringify(context.state.dict))
                Vue.prototype.errorCatchModal('字典请求出错：', err);
                reject(err)
            })
        })
    }
}
const getters = {}
export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}


