const app = {
    namespaced: true,
    state: {
        loading: false,
        zfLoading: false,
        userInfo: {},
        queryNum: 0,
        notEdit: false,
        notEditText: "",
    },
    mutations: {
        showLoading(state) {
            state.queryNum++;
            state.loading = true
        },
        hideLoading(state) {
            state.queryNum--;
            state.queryNum = state.queryNum <= 0 ? 0 : state.queryNum;
            if (state.queryNum == 0) state.loading = false
        },
        showZfLoading(state) {
            state.zfLoading = true
        },
        hideZfLoading(state) {
            state.zfLoading = false
        },
        setWeixinCreditRes(state) {
            state.weixin = false
        },
        setUserInfo(state, data) {
            state.userInfo = data
        },
        rejectEdit(state, data) {
            state.notEdit = true;
            state.notEditText = data;
        },
    },
    getters: {}
};

export default app;


