import request from "./request";

var querystring = require('querystring');
const urls = {
    ORDER_STATUS_QUERY: "/mogo/mogox/home/orderStatsQuery.jjs",
    NOTICE_INFO_QUERY: "/mogo/mogox/home/noticeInfoQuery.jjs",
    SHORTCUT_MENU: "/mogo/mogox/home/shortcutMenuList.jjs",
    PERSONAL_GTASKS_STATS: "/mogo/mogox/home/personalGtasksStats.jjs",
    PERSONAL_GTASKS_LIST: "/mogo/mogox/home/personalGtasksList.jjs?aa=111212",
    TAKE_TASK: "/mogo/mogox/home/takeTask.jjs",
    CHART_QUERY: "/mogo/mogox/home/chartQuery.jjs",
}
export const ORDER_STATUS_QUERY = params => {
    return request.post(urls.ORDER_STATUS_QUERY, params)
}
export const NOTICE_INFO_QUERY = params => {
    return request.post(urls.NOTICE_INFO_QUERY, params)
}
export const SHORTCUT_MENU = params => {
    return request.post(urls.SHORTCUT_MENU, params)
}
export const PERSONAL_GTASKS_STATS = params => {
    return request.post(urls.PERSONAL_GTASKS_STATS, params)
}
export const PERSONAL_GTASKS_LIST = params => {
    return request.post(urls.PERSONAL_GTASKS_LIST, params)
}
export const TAKE_TASK = params => {
    return request.post(urls.TAKE_TASK, params)
}
export const CHART_QUERY = params => {
    return request.post(urls.CHART_QUERY, params)
}


