import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

Vue.use(Router)

const router = new Router({
    routes: [
        {
            path: '/',
            name: 'index',
            redirect: 'home',
        },
        {
            path: '/menu',
            name: 'menu',
            component: function (resolve) {
                require(['@/components/EntryIndex'], resolve);
            }
        },
        {
            path: '/shortcutMenu',
            name: 'shortcutMenu',
            component: function (resolve) {
                require(['@/components/ShortcutMenu'], resolve);
            }
        },
        {
            path: '/home',
            name: 'home',
            component: function (resolve) {
                require(['../views/home/index.vue'], resolve);
            }
        },
        {
            path: '/404',
            name: '404',
            component: function (resolve) {
                require(['@/components/404.vue'], resolve);
            }
        },
        {
            path: '/*',
            redirect: '404',
        },
    ]
})
